
import React, {Component} from 'react';
import '../Styles/Footer.css';
import instagram_logo from '../media/logo_instagram.svg';
import facebook_logo from '../media/logo_facebook.svg';
import logo from '../media/jem_logo_white.svg';
import * as d3 from 'd3';
import rights from '../media/equinox_rights.svg';
import {connect} from 'react-redux';
import { bindActionCreators } from 'redux';
import equinoxUA from '../media/Ua.svg'
import equinoxEN from '../media/En.svg'

class Footer extends Component {

    constructor(props){
        super(props);
    }

    componentDidMount(){
        this.preSets();
        window.scrollTo(0, 0);
    }

    preSets(){
        this.followAnimStyles();
    }

    followAnimStyles(){
        d3.selectAll('.footer_follows a')
        .on('mouseover', function(){
            d3.select(this).selectAll('path')
                .transition().duration(200).ease(d3.easeLinear)
                .style('fill', '#FFB100');
        })
        .on('mouseleave', function(){
            d3.select(this).selectAll('path')
                .transition().duration(200).ease(d3.easeLinear)
                .style('fill', '#fff');
        })
    }

    changeLang = lang => {
        const ls = localStorage;
        if(lang === 'ru'){
            ls.setItem('lang', 'ru')
            this.setLangFooter('русский');
            this.props.changeToRu()
        } else {
            ls.setItem('lang', 'en')
            this.setLangFooter('english');
            this.props.changeToEn()
        }
    }

    setLangFooter(lang){
        d3.selectAll('.footer_lang span')
            .each(function(){
                let e = d3.select(this);
                if(e.node().innerHTML.toLowerCase() === lang) {
                    e.style('color', '#FFB100')
                     .style('text-decoration', 'underline');
                } else {
                    e.style('color', '#fff')
                     .style('text-decoration', 'none');
                }
            });
            if(lang === 'русский'){
                this.setLangHeader('ru');
            }else{
                this.setLangHeader('en');
            }
    }

    setLangHeader(lang){
        d3.selectAll('.header_lang_box span')
            .each(function(){
                let e = d3.select(this);
                if(e.node().innerHTML.toLowerCase() === lang) {
                    e.style('color', '#FFB100')
                     .style('text-decoration', 'underline');
                } else {
                    e.style('color', '#5C5C5C')
                     .style('text-decoration', 'none');
                }
            })
    }

    render(){
        return(
            <section className='footer'>
                <div className='container'>
                    <div className='footer_box_line'>
                        <div className='footer_box'>
                            <div className='footer_box_line'>
                                <div className='footer_box footer_contacts'>
                                    { this.props.lang === 'en' ? <h4>GET IN TOUCH WITH US:</h4> : <h4>Свяжитесь с нами:</h4>}
                                    <a href='tel:+380 507776669' style={{color: '#fff', textDecoration: 'none'}}>+38 050 777 6669</a>
                                    <a href='tel:+380 977776669' style={{color: '#fff', textDecoration: 'none'}}>+38 097 777 6669</a>
                                    <a href='href="mailto:@jem.productions"' style={{color: '#fff', textDecoration: 'none'}}>info@jem.productions</a>
                                </div>
                                <div className='footer_box'>
                                    <h4>{this.props.lang === 'en' ? 'FOLLOW US:' : 'ПОДПИШИТЕСЬ НА НАС:'}</h4>
                                    <div className='footer_box_line footer_follows'>
                                        <a href='https://www.instagram.com/jem.productions/' target="_blank">
                                            <svg id="logo-instagram" xmlns="http://www.w3.org/2000/svg" width="87" height="87" viewBox="0 0 87 87">
                                                <path id="Path_26" data-name="Path 26" d="M125.625,71.25A18.178,18.178,0,0,1,143.75,89.375v36.25a18.178,18.178,0,0,1-18.125,18.125H89.375A18.178,18.178,0,0,1,71.25,125.625V89.375A18.178,18.178,0,0,1,89.375,71.25h36.25m0-7.25H89.375A25.45,25.45,0,0,0,64,89.375v36.25A25.45,25.45,0,0,0,89.375,151h36.25A25.45,25.45,0,0,0,151,125.625V89.375A25.45,25.45,0,0,0,125.625,64Z" transform="translate(-64 -64)" fill="#fff"/>
                                                <path id="Path_27" data-name="Path 27" d="M205.313,138.875a5.438,5.438,0,1,1,5.438-5.437A5.424,5.424,0,0,1,205.313,138.875ZM181.75,142.5a14.5,14.5,0,1,1-14.5,14.5,14.513,14.513,0,0,1,14.5-14.5m0-7.25A21.75,21.75,0,1,0,203.5,157,21.756,21.756,0,0,0,181.75,135.25Z" transform="translate(-138.25 -113.5)" fill="#fff"/>
                                                <path id="Path_28" data-name="Path 28" d="M116.4,19.907v7.814a2.077,2.077,0,0,0,1.93,2.141h0a2,2,0,0,0,1.93-2.141h0a.732.732,0,0,1,.681-.749H122.3a.732.732,0,0,1,.681.749v6.422a2.733,2.733,0,0,0,2.611,2.89h0a2.733,2.733,0,0,0,2.611-2.89L128.091,19.8Z" transform="translate(-74.504 39.368)" fill="#fff"/>
                                            </svg>
                                        </a>
                                        <a href='https://facebook.com/jem.productions.ukraine/' target="_blank">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="87" height="87" viewBox="0 0 87 87">
                                                <path id="logo-facebook" d="M146.2,64H68.8A4.806,4.806,0,0,0,64,68.8V146.2a4.806,4.806,0,0,0,4.8,4.8h38.7V116.563H97.1V103.875h10.4V94.5c0-11.238,7.794-17.355,17.83-17.355,4.8,0,9.969.362,11.17.521V89.4h-8c-5.46,0-6.5,2.583-6.5,6.389v8.088h13l-1.7,12.688H122V151h24.2a4.806,4.806,0,0,0,4.8-4.8V68.8a4.806,4.806,0,0,0-4.8-4.8Z" transform="translate(-64 -64)" fill="#fff"/>
                                            </svg>
                                        </a>
                                    </div>
                                </div>
                            </div>
                            <p className='footer_info'>{this.props.lang === 'en' ? 'Professional team, marvelous  locations, technical possibilities,  affordable prices will make your experience unforgettable!' : 'Профессиональная команда, удивительные локации, технические возможности и приемлемая цена при создании незабываемых эмоций!'}</p>
                            <div className='footer_box_line footer_lang'>
                                <span onClick={() => this.changeLang('en')}>English</span>
                                <span onClick={() => this.changeLang('ru')}>Русский</span>
                            </div>
                        </div>
                        <div className='footer_box'>
                            <img src={logo} alt='' />
                        </div>
                    </div>
                    <p>© JEM Production™, 2019 - Privacy Policy </p>
                    <a href="https://www.equinox.company" target="_blank"><img class='equinox_rights' src={this.props.lang === 'en' ? equinoxEN : equinoxUA} alt='' /></a>
                </div>
            </section>
        )
    }

}

const mapStateToProps = (state) => {
    return {
        lang: state.lang.currentLang
    }
};

const mapDispatchToProps = dispatch => (
    bindActionCreators({
      changeToRu: () => dispatch({ type: 'CHANGE_TO_RU' }),
      changeToEn: () => dispatch({ type: 'CHANGE_TO_EN' })
    }, dispatch)
  );

export default connect(mapStateToProps, mapDispatchToProps)(Footer);
