import React, {Component} from 'react';
import Header from './Header'; /* Adaptive */
import ServiceBar from './ServiceBar'; /* Adaptive */
import Footer from './Footer'; /* Adaptive */
import '../Styles/Service.css';
import { seoActions } from '../actions/seoActions';
import MetaTags from "react-meta-tags";
import { connect } from 'react-redux';
import { initGA, logPageView } from '../analytics';
const getSeoByUrl = seoActions.getSeoByUrl;

class Service extends Component {

    constructor(props){
        super(props);
     
    }

    componentDidMount(){
        initGA();
        this.props.getSeoByUrl('service')
        .then(() => {
            try{
                logPageView(this.props.getMetaResult.result.title)
            }catch(e){
                console.log(e)
            }
        });

    }



    render(){
        let metaTags = this.props.getMetaResult && this.props.getMetaResult.result;
        return(
            <div className='service_page'>
 <MetaTags>
            <title>{metaTags && metaTags.title}</title>
            <meta name="description" content={metaTags && metaTags.description} />
            <meta name="keywords" content={metaTags && metaTags.keywords} />
            </MetaTags>
                <Header page={this.props.lang === 'en' ? 'service' : ' сервис'}/>
                <section className='service_preview'>
                    <div className='container'>
                        <h2>{this.props.lang === 'ru' ? 'Продакшн в Украине':'OFFERING LOCAL PRODUCTION'}</h2>
                        <p>{this.props.lang === 'ru' ? 'Профессиональная команда, чудесные локации, технические возможности, доступные цены сделают ваш творческий продукт незабываемым!':'Our company offers a full range of services on organising every need during production period.'}</p>
                    </div>
                </section>
                <section className='service_info'>
                    <div className='container'>
                        <ServiceBar/>
                    </div>
                </section>
                <Footer/>
            </div>
        )
    }

}

const mapStateToProps = (state) => {
    return {
        lang: state.lang.currentLang,
        getMetaResult: state.seo.getSeoByUrlResult
    }
};

export default connect(mapStateToProps, { getSeoByUrl })(Service);