import React, {Component} from 'react';
import { Redirect, Link } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer';
import video from '../media/video/test_video.mp4';

import * as d3 from 'd3';
import '../Styles/VideoView.css';
import { apiUrl } from '../config';
import { seoActions } from '../actions/seoActions';
import MetaTags from "react-meta-tags";
import test_img from '../media/project_test_image.jpg';
import { connect } from 'react-redux';
import {  browserHistory} from 'react-router';

const getSeoByUrl = seoActions.getSeoByUrl;




class VideoView extends Component {

    constructor(props){
        super(props);
        this.state = {
            error: false
        }
    }

    componentDidMount(){
        const portfolioUrl = this.props.match.url.split('/')[2];
        fetch(`${apiUrl}/portfolio/${portfolioUrl}`)
            .then(res => {
                if(res.status === 404) {
                    this.setState({ error: true })
                }
                else return res.json();
            })
            .then(portfolio => this.setState({portfolio}, () => console.log(this.state.portfolio)))
    }
    

    


    render(){
        if(!this.state.error){
        return(
            <div className='blog_post_view'>
                <Header page={this.props.lang === 'en' ? 'work' : 'работы'}/>
                {console.log(this.props)}
                <section className='video_section'>
                {
                    this.state.portfolio && 
                    <div className='video_container'>
                        
                        <h2>{this.state.portfolio.name}</h2>
                        <h3>{this.state.portfolio.customer}</h3>
                        <Link className='post_button blog_post_exit_button'>
                            <span onClick={browserHistory.goBack} style={{color: '#000'}}>{this.props.lang === 'ru' ? 'Назад':'Go back'}</span>
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 21.767 31.777">
                                <path fill='#fff' d="M116.4,20V34.406a3.829,3.829,0,0,0,3.558,3.947h0a3.692,3.692,0,0,0,3.558-3.947h0a1.35,1.35,0,0,1,1.256-1.382h2.512a1.35,1.35,0,0,1,1.256,1.382V46.248a5.04,5.04,0,0,0,4.814,5.329h0a5.04,5.04,0,0,0,4.814-5.329L137.958,19.8Z" transform="translate(-116.4 -19.8)"/>
                            </svg>
                        </Link>
                        <div style={{display:'flex', justifyContent:'center'}} dangerouslySetInnerHTML={{__html: this.state.portfolio.video}}></div>
                        {/* <video controls preload='metadata'>
                            <source src={this.state.portfolio.video} type='video/mp4'></source>
                        </video> */}
                        <ul className='video_info_list' dangerouslySetInnerHTML={{__html: this.props.lang === 'ru' ? this.state.portfolio.description : this.state.portfolio.descriptionEn }}>
                            {/* <li><span>Photographer: </span>Albert Breht</li>
                            <li><span>Agency: </span><br/>F-Incorp Studio</li>
                            <li><span>Art director: </span>Robert Wede</li>
                            <li><span>We create: </span>Full production service in Ukrain and area.</li> */}
                        </ul>
                   </div>
                }
                </section>
                <Footer/>
            </div>
        )}else {
            return(<Redirect to="/work"/>)
        }
    }

}

const mapStateToProps = (state) => {
    return {
        lang: state.lang.currentLang
    }
};

export default connect(mapStateToProps, { getSeoByUrl })(VideoView);