const initialState = {
  currentLang: 'en'
};

export const languageReducer = (state = initialState, action) => {
  switch(action.type){
    case 'CHANGE_TO_RU':
      return{
        ...state,
        currentLang: 'ru'
      }
    case 'CHANGE_TO_EN':
    return{
      ...state,
      currentLang: 'en'
    }
    default: return state
  }
}
